@charset "UTF-8";
@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Light.woff2") format("woff2"), url("../fonts/Raleway-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Thin.woff2") format("woff2"), url("../fonts/Raleway-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
}
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Raleway";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.ibg {
  position: relative;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

body {
  color: #c52743;
  background-color: #dfe1dc;
  font-family: "Raleway";
  font-size: 40px;
  font-style: normal;
  font-weight: 300;
}
@media (max-width: 1600px) {
  body {
    font-size: calc(15px + 25 * ((100vw - 320px) / 1280));
  }
}

.wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

._container {
  margin: 0 auto;
  height: 100%;
}

.video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
}
@media screen and (orientation: portrait) {
  .video {
    align-items: flex-start;
  }
}
.video__items {
  width: 80%;
  height: 80%;
  position: relative;
}
@media screen and (orientation: portrait) {
  .video__items {
    width: 100%;
    height: 80%;
  }
}
.video__item {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
}
.video video {
  object-fit: contain;
}

.displayed {
  opacity: 1;
  transition: all 2s ease;
}

.page {
  width: 100%;
  height: 100%;
}
.page__wrapper {
  display: flex;
  width: 100%;
  height: 100%;
}
@media screen and (orientation: portrait) {
  .page__wrapper {
    flex-direction: row;
  }
}
@media screen and (orientation: landscape) {
  .page__wrapper {
    flex-direction: column;
    align-items: center;
  }
}
.page__screen {
  flex: 1 0 100%;
  position: relative;
  padding: 0 15px;
}
.page__pagination {
  position: absolute;
  transform: translate(0px, -50%);
  display: flex;
}
@media screen and (orientation: portrait) {
  .page__pagination {
    flex-direction: row;
    left: 50%;
    bottom: 10px;
    transform: translate(-50%, 0);
  }
}
@media screen and (orientation: landscape) {
  .page__pagination {
    flex-direction: column;
    right: 10px;
    top: 50%;
  }
}
.page__bullet {
  width: 15px;
  height: 15px;
  margin: 10px;
  background-color: #c52743;
  border-radius: 50%;
  transition: background-color 0.3s ease 0s;
  opacity: 0.3;
}
@media (max-width: 1600px) {
  .page__bullet {
    width: calc(10px + 5 * ((100vw - 320px) / 1280));
  }
}
@media (max-width: 1600px) {
  .page__bullet {
    height: calc(10px + 5 * ((100vw - 320px) / 1280));
  }
}
@media (max-width: 1600px) {
  .page__bullet {
    margin: calc(5px + 5 * ((100vw - 320px) / 1280));
  }
}
.page__bullet_active {
  opacity: 1;
  background-color: #c52743;
}

.screen__content {
  position: relative;
  z-index: 2;
  text-align: center;
}
.screen__title {
  padding-top: 20px;
  display: inline-block;
}

.scroll-indicator-next {
  position: absolute;
  bottom: 40%;
  left: 15px;
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
  transform: translateY(100%);
}
@media (max-width: 1600px) {
  .scroll-indicator-next {
    left: calc(15px + 0 * ((100vw - 320px) / 1280));
  }
}
@media screen and (orientation: portrait) {
  .scroll-indicator-next {
    transform: translateY(1px) translateX(-100%) rotate(-90deg);
    left: 80%;
    bottom: 30px;
  }
}
@media screen and (orientation: portrait) and (max-width: 1600px) {
  .scroll-indicator-next {
    bottom: calc(15px + 15 * ((100vw - 320px) / 1280));
  }
}
.scroll-indicator-next:hover .cls-3 {
  transform: rotate(180deg);
}
.scroll-indicator-next.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}
.scroll-indicator-next svg {
  width: 50px;
  height: 140px;
}
@media (max-width: 1600px) {
  .scroll-indicator-next svg {
    width: calc(35px + 15 * ((100vw - 320px) / 1280));
  }
}
@media (max-width: 1600px) {
  .scroll-indicator-next svg {
    height: calc(100px + 40 * ((100vw - 320px) / 1280));
  }
}

.scroll-indicator-prew {
  position: absolute;
  bottom: 60%;
  left: 15px;
  transform: rotate(180deg);
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
}
@media (max-width: 1600px) {
  .scroll-indicator-prew {
    left: calc(15px + 0 * ((100vw - 320px) / 1280));
  }
}
@media screen and (orientation: portrait) {
  .scroll-indicator-prew {
    transform: rotate(90deg);
    left: 20%;
    bottom: 30px;
  }
}
@media screen and (orientation: portrait) and (max-width: 1600px) {
  .scroll-indicator-prew {
    bottom: calc(15px + 15 * ((100vw - 320px) / 1280));
  }
}
.scroll-indicator-prew.swiper-button-disabled {
  opacity: 0;
  pointer-events: none;
}
.scroll-indicator-prew:hover .cls-3 {
  transform: rotate(180deg);
}
.scroll-indicator-prew svg {
  width: 50px;
  height: 140px;
}
@media (max-width: 1600px) {
  .scroll-indicator-prew svg {
    width: calc(35px + 15 * ((100vw - 320px) / 1280));
  }
}
@media (max-width: 1600px) {
  .scroll-indicator-prew svg {
    height: calc(100px + 40 * ((100vw - 320px) / 1280));
  }
}

.cls-3 {
  transition: all 1.5s ease 0s;
}
.cls-3.active {
  transform: rotate(360deg);
}

.cls-4 {
  stroke: #c52743;
}

.cls-5 {
  stroke: #c52743;
}

.cls-6 {
  fill: #c52743;
}

.scroll-indicator-next .cls-5 {
  transition: all 0.8s ease 0s;
  transform: translateY(-50%);
}
.scroll-indicator-next .cls-5.active {
  transform: translateY(0%);
}

.scroll-indicator-next .cls-6 {
  transition: all 0.8s ease 0s;
  transform: translateY(-50%);
}
.scroll-indicator-next .cls-6.active {
  transform: translateY(0%);
}