//@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400&display=swap');
@import"fonts.scss";
@import "modules/variables.scss";
@import "modules/nullstyle.scss";
@import"modules/mixin.scss";
// Подключение файлов стилей страниц и элементов
@import "modules/ui.scss";
//@import "modules/header.scss";
@import "modules/footer.scss";

body {
    color: $first_color;
    background-color: $second_color;
    font-family: $fontfamily;
    @include adaptiv-value("font-size", 40, 15, 0);
    font-style: normal;
    font-weight: 300;

}

// Оболочка
.wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;

}

// Ограничивающий контейнер
._container {
    margin: 0 auto;
    height: 100%;

}



.video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -1;

    @media screen and (orientation : portrait) {
        align-items: flex-start;
    }


    &__items {

        width: 80%;
        height: 80%;
        position: relative;



        @media screen and (orientation : portrait) {
            width: 100%;
            height: 80%;
        }

    }

    &__item {


        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        opacity: 0;


    }

    & video {
        object-fit: contain;

    }
}

.displayed {
    // z-index: 10;
    opacity: 1;
    transition: all 2s ease;
}


.page {

    width: 100%;
    height: 100%;

    &__wrapper {
        display: flex;

        @media screen and (orientation : portrait) {
            flex-direction: row;
        }

        @media screen and (orientation : landscape) {
            flex-direction: column;
            align-items: center;
        }

        width: 100%;
        height: 100%;


    }

    &__screen {
        flex: 1 0 100%;
        position: relative;
        // max-width: calc($container-width + $container-padding*2 );
        padding: 0 $container-padding;


    }

    &__pagination {
        position: absolute;


        transform: translate(0px, -50%);
        display: flex;

        @media screen and (orientation : portrait) {
            flex-direction: row;
            left: 50%;
            bottom: 10px;
            transform: translate(-50%, 0);
        }

        @media screen and (orientation : landscape) {
            flex-direction: column;
            right: 10px;
            top: 50%;
        }

    }

    &__bullet {
        @include adaptiv-value("width", 15, 10, 0);
        @include adaptiv-value("height", 15, 10, 0);
        @include adaptiv-value("margin", 10, 5, 0);

        background-color: $first_color;

        border-radius: 50%;
        transition: background-color 0.3s ease 0s;
        opacity: 0.3;

        &_active {
            opacity: 1;
            background-color: $first_color;
        }
    }
}


.screen {

    &__content {
        position: relative;
        z-index: 2;
        text-align: center;
    }

    &__title {
        padding-top: 20px;
        display: inline-block;
    }
}

.scroll-indicator-next {

    position: absolute;
    bottom: 40%;
    @include adaptiv-value("left", 15, 15, 0);
    opacity: 1;
    pointer-events: all;
    cursor: pointer;
    transform: translateY(100%);

    @media screen and (orientation : portrait) {
        transform: translateY(1px) translateX(-100%) rotate(-90deg);
        left: 80%;
        @include adaptiv-value("bottom", 30, 15, 0);

    }

    &:hover {
        .cls-3 {
            transform: rotate(180deg);
        }
    }

    &.swiper-button-disabled {
        opacity: 0;
        pointer-events: none;

    }

    & svg {
        @include adaptiv-value("width", 50, 35, 0);
        @include adaptiv-value("height", 140, 100, 0);
    }
}



.scroll-indicator-prew {

    position: absolute;
    bottom: 60%;
    @include adaptiv-value("left", 15, 15, 0);

    transform: rotate(180deg);
    opacity: 1;
    pointer-events: all;

    cursor: pointer;

    @media screen and (orientation : portrait) {
        transform: rotate(90deg);
        left: 20%;
        @include adaptiv-value("bottom", 30, 15, 0);

    }


    &.swiper-button-disabled {
        opacity: 0;
        pointer-events: none;

    }

    &:hover {
        .cls-3 {
            transform: rotate(180deg);
        }
    }

    & svg {
        @include adaptiv-value("width", 50, 35, 0);
        @include adaptiv-value("height", 140, 100, 0);

    }


}


.cls-2 {}

.cls-3 {
    transition: all 1.5s ease 0s;


    &.active {
        transform: rotate(360deg);
    }
}

.cls-4 {
    stroke: $first_color;
}

.cls-5 {
    stroke: $first_color;
}

.cls-6 {
    fill: $first_color;
}

.scroll-indicator-next .cls-5 {
    transition: all 0.8s ease 0s;
    transform: translateY(-50%);

    &.active {
        transform: translateY(0%);
    }
}

.scroll-indicator-next .cls-6 {
    transition: all 0.8s ease 0s;
    transform: translateY(-50%);

    &.active {
        transform: translateY(0%);
    }
}